.page{
    width: 100%;
    min-height: 100vh;
    background-color: #404258;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 500px;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.container h1{
    color: rgba(255, 255, 255, 0.589);
    margin: 5px 0;
}

.container img{
    width: 140px;
}

.container p{
    margin: 0;
    margin-top: 30px;
    color: rgba(255, 255, 255, 0.774);
}

.container button{
    margin-top: 20px;
    background-color: rgba(255, 255, 255, 0.384);
    padding: 10px 10px;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
}

.container button:hover{
    background-color: rgba(255, 255, 255, 0.589);
}

.text-group{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
}

.text-group input{
    padding: 7px 10px;
    border: none;
    border-radius: 3px;
    outline: none;
    font-size: 18px;
}

.text-group p{
    color: rgba(255, 255, 255, 0.774);
    margin: 0;
    font-size: 20px;
}

.result-div{
    position: relative;
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;
    min-width: 100%;
    margin-bottom: 40px;
}

.result-div p{
    margin: 0;
    color: rgb(255, 255, 255);
}

.result-div button{
    margin: 0;
    background-color: rgba(255, 255, 255, 0.384);
    padding: 5px;
}

.pop-up{
    position: absolute;
    top: 40px;
    background-color: green;
    background-color: green;
    padding: 10px;
    border-radius: 5px;
    color: white;
    width: fit-content;
    text-align: center;
}

.pop-up p{
    display: inline-block;
    margin: 0;
}